import { AppBar, Box } from "@mui/material";
import React from "react";

const Footer = (props) => {
    return (
        <>
            {props.darkMode === false && (
                <AppBar
                    position="static"
                    sx={{
                        mt: 6,
                        backgroundColor: props.typeColor ? props.typeColor : "#CCCCCC",
                        height: "64px",
                    }}
                >
                    <Box
                        color={"black"}
                        sx={{
                            textAlign: "right",
                            ml: "auto",
                        }}
                        mr={2}
                        mt={1}
                    >
                        版本：v1.5
                        <br />
                        最後更新時間：2024/8/31
                    </Box>
                </AppBar>
            )}
            {props.darkMode === true && (
                <AppBar
                    position="static"
                    sx={{
                        mt: 6,
                        backgroundColor: "black",
                        height: "64px",
                    }}
                >
                    <Box
                        color="#EEEEEE"
                        sx={{
                            textAlign: "right",
                            ml: "auto",
                        }}
                        mr={2}
                        mt={1}
                    >
                        版本：v1.7
                        <br />
                        最後更新時間：2024/8/31
                    </Box>
                </AppBar>
            )}
        </>
    );
};

export default Footer;
