import { AppBar, Box, Button, Toolbar, Typography } from "@mui/material";
import { useState } from "react";

const Header = (props) => {
    const [currentPage, setCurrentPage] = useState("page1");

    const handlePageChange = (page) => {
        props.onPageChange(page);
        setCurrentPage(page);
        console.log(`Current page: ${page}`);
    };

    return (
        <>
            {props.darkMode === false && (
                <AppBar
                    position="static"
                    sx={{
                        mb: 3,
                        backgroundColor: props.typeColor ? props.typeColor : "#CCCCCC",
                        height: "64px",
                    }}
                >
                    <Toolbar sx={{ height: "100%" }}>
                        <Box display="flex" alignItems="center" sx={{ flexGrow: 1 }}>
                            <Typography variant="h6" sx={{ flexShrink: 0 }}>
                                <Box fontWeight={"bold"} color="#333333" sx={{ fontSize: "clamp(18px, 2vw, 22px)" }}>
                                    柯南集數查詢
                                </Box>
                            </Typography>

                            <Box display="flex" gap={1} ml={4}>
                                <Button
                                    sx={{
                                        backgroundColor: "transparent",
                                        color: currentPage === "page1" ? "#333333" : "#888888",
                                        border: "none",
                                        fontWeight: currentPage === "page1" ? "bold" : "bold",
                                        fontSize: "clamp(16px, 2vw, 20px)",
                                        height: "100%",
                                        "&:hover": {
                                            backgroundColor: "transparent",
                                            textDecoration: "underline",
                                        },
                                    }}
                                    onClick={() => {
                                        handlePageChange("page1");
                                        props.setResetPage(true);
                                    }}
                                >
                                    集數列表
                                </Button>
                                <Button
                                    sx={{
                                        backgroundColor: "transparent",
                                        color: currentPage === "page2" ? "#333333" : "#888888",
                                        border: "none",
                                        fontWeight: currentPage === "page2" ? "bold" : "normal",
                                        fontSize: "clamp(16px, 2vw, 20px)",
                                        height: "100%",
                                        "&:hover": {
                                            backgroundColor: "transparent",
                                            textDecoration: "underline",
                                        },
                                    }}
                                    onClick={() => {
                                        handlePageChange("page2");
                                        props.setResetPage(true);
                                    }}
                                >
                                    角色出場查詢
                                </Button>
                            </Box>
                        </Box>
                    </Toolbar>
                </AppBar>
            )}
            {props.darkMode === true && (
                <AppBar
                    position="static"
                    sx={{
                        mb: 3,
                        backgroundColor: "black",
                        height: "64px",
                    }}
                >
                    <Toolbar sx={{ height: "100%" }}>
                        <Box display="flex" alignItems="center" sx={{ flexGrow: 1 }}>
                            <Typography variant="h6" sx={{ flexShrink: 0 }}>
                                <Box fontWeight={"bold"} color="#EEEEEE" sx={{ fontSize: "clamp(18px, 2vw, 22px)" }}>
                                    柯南集數查詢
                                </Box>
                            </Typography>

                            <Box display="flex" gap={1} ml={4}>
                                <Button
                                    sx={{
                                        backgroundColor: "transparent",
                                        color: currentPage === "page1" ? "#EEEEEE" : "#888888",
                                        border: "none",
                                        fontWeight: currentPage === "page1" ? "bold" : "bold",
                                        fontSize: "clamp(16px, 2vw, 20px)",
                                        height: "100%",
                                        "&:hover": {
                                            backgroundColor: "transparent",
                                            textDecoration: "underline",
                                        },
                                    }}
                                    onClick={() => {
                                        handlePageChange("page1");
                                        props.setResetPage(true);
                                    }}
                                >
                                    集數列表
                                </Button>
                                <Button
                                    sx={{
                                        backgroundColor: "transparent",
                                        color: currentPage === "page2" ? "#EEEEEE" : "#888888",
                                        border: "none",
                                        fontWeight: currentPage === "page2" ? "bold" : "normal",
                                        fontSize: "clamp(16px, 2vw, 20px)",
                                        height: "100%",
                                        "&:hover": {
                                            backgroundColor: "transparent",
                                            textDecoration: "underline",
                                        },
                                    }}
                                    onClick={() => {
                                        handlePageChange("page2");
                                        props.setResetPage(true);
                                    }}
                                >
                                    角色出場查詢
                                </Button>
                            </Box>
                        </Box>
                    </Toolbar>
                </AppBar>
            )}
        </>
    );
};

export default Header;
