import { Box, Container } from "@mui/material";
import { grey } from "@mui/material/colors";
import Papa from "papaparse";
import { useEffect, useState } from "react";
import "./App.css";
import CharacterAppeared from "./CharacterAppeared";
import EpisodeList from "./EpisodeList";
import Footer from "./Footer";
import Header from "./Header";

function App() {
    const [darkMode, setDarkMode] = useState(false);

    const [episodesData, setEpisodesData] = useState({ episodesIndexTW: [], episodesIndexJP: [], episodesNameTW: [], episodesNameJP: [], broadcastTime: [], ratings: [], mangaVolume: [] });
    const [episodesType, setEpisodesType] = useState({});
    const [charatersList, setCharatersList] = useState({});
    const [charatersData, setCharatersData] = useState({});

    const [typeColor, setTypeColor] = useState(grey[100]);
    const [currentPage, setCurrentPage] = useState("page1");

    const [resetPage, setResetPage] = useState(false);

    useEffect(() => {
        fetchCsvData();
    }, []);

    const fetchCsvData = async () => {
        fetch("https://tim6992481.github.io/conan-episode-queryer/episodesData.csv")
            .then((response) => response.text())
            .then((csvData) => {
                const episodesDataParsed = parseEpisodesData(csvData);
                setEpisodesData(episodesDataParsed);
            })
            .catch((error) => console.error("Error loading CSV file:", error));

        fetch("https://tim6992481.github.io/conan-episode-queryer/episodesType.csv")
            .then((response) => response.text())
            .then((csvData) => {
                const episodesTypeParsed = parseEpisodesType(csvData);
                setEpisodesType(episodesTypeParsed);
                console.log(episodesTypeParsed);
            })
            .catch((error) => console.error("Error loading CSV file:", error));

        fetch("https://tim6992481.github.io/conan-episode-queryer/charactersList.csv")
            .then((response) => response.text())
            .then((csvData) => {
                const charactersListParsed = parseCharactersList(csvData);
                setCharatersList(charactersListParsed);
            })
            .catch((error) => console.error("Error loading CSV file:", error));

        fetch("https://tim6992481.github.io/conan-episode-queryer/charactersData.csv")
            .then((response) => response.text())
            .then((csvData) => {
                const charactersDataParsed = parseCharactersData(csvData);
                setCharatersData(charactersDataParsed);
            })
            .catch((error) => console.error("Error loading CSV file:", error));
    };

    const parseEpisodesData = (csvData) => {
        const result = Papa.parse(csvData, {
            header: true,
            skipEmptyLines: true,
        });

        const parsedData = {};

        result.meta.fields.forEach((field) => {
            parsedData[field] = [];
        });

        result.data.forEach((row) => {
            result.meta.fields.forEach((field) => {
                parsedData[field].push(row[field]);
            });
        });

        return parsedData;
    };

    const parseEpisodesType = (csvData) => {
        const result = {};

        Papa.parse(csvData, {
            header: false,
            skipEmptyLines: true,
            complete: function (parsedData) {
                parsedData.data.forEach((row) => {
                    if (row.length === 2) {
                        const [key, value] = row;
                        result[key] = value;
                    }
                });
            },
        });

        return result;
    };

    const parseCharactersList = (csvData) => {
        const result = {};

        Papa.parse(csvData, {
            header: false,
            skipEmptyLines: true,
            complete: function (parsedData) {
                parsedData.data.forEach((row) => {
                    if (row.length === 2) {
                        const [key, value] = row;
                        result[key] = value;
                    }
                });
            },
        });

        return result;
    };

    const parseCharactersData = (csvData) => {
        const result = Papa.parse(csvData, {
            header: true,
            skipEmptyLines: true,
        });

        const parsedData = {};

        result.meta.fields.forEach((field) => {
            parsedData[field] = [];
        });

        result.data.forEach((row) => {
            result.meta.fields.forEach((field) => {
                parsedData[field].push(row[field]);
            });
        });

        return parsedData;
    };

    const handlePageChange = (page) => {
        setCurrentPage(page);
        setTypeColor(grey[100]);
    };

    return (
        <>
            <Box display="flex" flexDirection="column" minHeight="100vh">
                <Header onPageChange={handlePageChange} typeColor={typeColor} setResetPage={setResetPage} darkMode={darkMode} />
                <Container maxWidth={"lg"} sx={{ flexGrow: 1 }}>
                    <Box>
                        {currentPage === "page1" && (
                            <EpisodeList
                                episodesData={episodesData}
                                episodesType={episodesType}
                                charatersList={charatersList}
                                charatersData={charatersData}
                                setTypeColor={setTypeColor}
                                resetPage={resetPage}
                                setResetPage={setResetPage}
                                darkMode={darkMode}
                                setDarkMode={setDarkMode}
                            />
                        )}
                        {currentPage === "page2" && (
                            <CharacterAppeared
                                episodesData={episodesData}
                                episodesType={episodesType}
                                charatersList={charatersList}
                                charatersData={charatersData}
                                resetPage={resetPage}
                                setResetPage={setResetPage}
                                darkMode={darkMode}
                                setDarkMode={setDarkMode}
                            />
                        )}
                    </Box>
                </Container>
                <Footer typeColor={typeColor} darkMode={darkMode} />
            </Box>
        </>
    );
}

export default App;
