import { DarkMode, LightMode } from "@mui/icons-material";
import CloseIcon from "@mui/icons-material/Close";
import ViewModuleIcon from "@mui/icons-material/ViewModule";
import ViewStreamIcon from "@mui/icons-material/ViewStream";
import { Box, Button, createTheme, CssBaseline, Dialog, DialogContent, DialogTitle, Grid, IconButton, MenuItem, Select, styled, ThemeProvider, Tooltip, tooltipClasses } from "@mui/material";
import { blue, green, grey, pink, purple, red, yellow } from "@mui/material/colors";
import React, { useEffect, useState } from "react";
import EpisodeDetail from "./EpisodeDetail";

const lightTheme = createTheme({
    palette: {
        background: {
            default: "white",
        },
        text: {
            primary: "black",
        },
    },
});

const darkTheme = createTheme({
    palette: {
        background: {
            default: "#111111",
        },
        text: {
            primary: "white",
        },
    },
});

const LightTooltip = styled(({ className, ...props }) => <Tooltip {...props} classes={{ popper: className }} />)(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
        backgroundColor: theme.palette.common.white,
        color: "rgba(0, 0, 0, 0.85)",
        boxShadow: theme.shadows[1],
        fontSize: 18,
    },
}));

const dispColor = (type, conc) => {
    const conc1 = [grey[100], red[50], pink[50], blue[50], yellow[100], green[50], grey[100], purple[50]];
    const conc2 = [grey[700], red[500], pink[300], blue[500], yellow[600], green[500], grey[500], purple[500]];
    const conc3 = [grey[900], red[900], pink[800], blue[700], yellow[800], green[700], grey[700], purple[700]];

    const index = parseInt(type.substring(1));

    let color;
    switch (conc) {
        case 1:
            color = conc1[index];
            break;
        case 2:
            color = conc2[index];
            break;
        case 3:
            color = conc3[index];
            break;
        default:
            color = conc1[index];
    }

    return color;
};

const EpisodeList = (props) => {
    const episodesData = props.episodesData;
    const episodesType = props.episodesType;
    const charatersList = props.charatersList;
    const charatersData = props.charatersData;

    const [openDetail, setOpenDetail] = useState(false);
    const [detailData, setDetailData] = useState({ episodeDetail: {}, characterDetail: {} });

    const [type, setType] = useState("e0");
    const [currentEpisodes, setCurrentEpisodes] = useState([]);

    const [listStyle, setListStyle] = useState(0); // 0: 簡單列表 1: 詳細列表

    const [currentItems, setCurrentItems] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(0);

    const itemsPerPage = 100;

    useEffect(() => {
        setCurrentEpisodes(episodesData.episodesIndexTW || []);
        setCurrentItems(episodesData.episodesIndexTW.slice((currentPage - 1) * itemsPerPage, currentPage * itemsPerPage));
        setTotalPages(Math.ceil((episodesData.episodesIndexTW || []).length / itemsPerPage));
    }, [episodesData]);

    useEffect(() => {
        let selectedTypeArray = [];

        if (type !== "e0") {
            selectedTypeArray = charatersData[type];
            const filteredEpisodes = episodesData.episodesIndexTW.filter((_, index) => selectedTypeArray[index] === "T");
            setCurrentEpisodes(filteredEpisodes);
            setCurrentItems(filteredEpisodes.slice((1 - 1) * itemsPerPage, 1 * itemsPerPage));
            setTotalPages(Math.ceil(filteredEpisodes.length / itemsPerPage));
            setCurrentPage(1);
        } else {
            setCurrentEpisodes(episodesData.episodesIndexTW);
            setCurrentItems(episodesData.episodesIndexTW.slice((1 - 1) * itemsPerPage, 1 * itemsPerPage));
            setTotalPages(Math.ceil(episodesData.episodesIndexTW.length / itemsPerPage));
            setCurrentPage(1);
        }
    }, [type]);

    useEffect(() => {
        setType("e0");
        props.setResetPage(false);
    }, [props.resetPage]);

    useEffect(() => {
        console.log(listStyle);
    }, [listStyle]);

    const handlePreviousPage = () => {
        if (currentPage > 1) {
            let newCurrentPage = currentPage - 1;
            setCurrentPage(newCurrentPage);
            setCurrentItems(currentEpisodes.slice((newCurrentPage - 1) * itemsPerPage, newCurrentPage * itemsPerPage));
        }
    };

    const handleNextPage = () => {
        if (currentPage < totalPages) {
            let newCurrentPage = currentPage + 1;
            setCurrentPage(newCurrentPage);
            setCurrentItems(currentEpisodes.slice((newCurrentPage - 1) * itemsPerPage, newCurrentPage * itemsPerPage));
        }
    };

    const handlePageSelect = (event) => {
        let newCurrentPage = event.target.value;
        setCurrentPage(newCurrentPage);
        setCurrentItems(currentEpisodes.slice((newCurrentPage - 1) * itemsPerPage, newCurrentPage * itemsPerPage));
    };

    const handleTypeChange = (newType) => {
        setType(newType);
        props.setTypeColor(dispColor(newType, 1));
    };

    const onShowEpisodeDetails = (index) => {
        let episodeDetail = {};

        for (const key in episodesData) {
            if (episodesData.hasOwnProperty(key)) {
                if (episodesData[key].length > index) {
                    episodeDetail[key] = episodesData[key][index];
                } else {
                    episodeDetail[key] = null;
                }
            }
        }

        let characterDetail = {};

        for (const key in charatersData) {
            if (charatersData.hasOwnProperty(key)) {
                if (charatersData[key].length > index) {
                    characterDetail[key] = charatersData[key][index];
                } else {
                    characterDetail[key] = null;
                }
            }
        }

        let detailData = { episodeDetail: episodeDetail, characterDetail: characterDetail, episodesType: episodesType, charatersList: charatersList };

        setDetailData(detailData);
        // console.log(detailData);
    };

    const onSetListStyle = (type) => {
        setListStyle(type);
    };

    const onSetDarkMode = (mode) => {
        props.setDarkMode(mode);
    };

    return (
        <>
            <Dialog open={openDetail} maxWidth="lg" fullWidth>
                <DialogTitle sx={{ display: "flex", backgroundColor: props.darkMode === false ? "#555555" : "#111111", color: "white", paddingBottom: 1 }}>
                    <Box flexGrow={1} fontWeight={"bold"}>
                        {detailData.episodeDetail.episodesNameTW} / {detailData.episodeDetail.episodesNameJP}
                    </Box>

                    <Box>
                        <IconButton onClick={() => setOpenDetail(false)}>
                            <CloseIcon sx={{ color: "white" }} />
                        </IconButton>
                    </Box>
                </DialogTitle>
                <DialogContent sx={{ backgroundColor: props.darkMode === false ? "white" : "#222222", color: props.darkMode === false ? "black" : "#EEEEEE" }}>
                    <EpisodeDetail detailData={detailData} darkMode={props.darkMode} />
                </DialogContent>
            </Dialog>

            {props.darkMode === false && (
                <ThemeProvider theme={lightTheme}>
                    <CssBaseline />
                </ThemeProvider>
            )}

            {props.darkMode === true && (
                <ThemeProvider theme={darkTheme}>
                    <CssBaseline />
                </ThemeProvider>
            )}

            <Box mb={2} display="flex" justifyContent="flex-end">
                <Button
                    variant={props.darkMode === false ? "outlined" : "outlined"}
                    startIcon={props.darkMode === false ? <LightMode /> : <DarkMode />}
                    onClick={() => onSetDarkMode(!props.darkMode)}
                    sx={{
                        borderColor: "#000000",
                        borderRadius: 1,
                        borderWidth: 1,
                        backgroundColor: props.darkMode === false ? "#FFFFFF" : "#666666",
                        color: props.darkMode === false ? "#000000" : "#FFFFFF",
                        "&:hover": {
                            backgroundColor: props.darkMode === false ? "#DDDDDD" : "#888888",
                            borderColor: "#000000",
                            borderRadius: 1,
                            borderWidth: 1,
                        },
                    }}
                >
                    <Box fontWeight={"bold"}>切換模式</Box>
                </Button>
            </Box>

            <Grid container spacing={2} justifyContent="center" mb={5}>
                {["e0", "e1", "e2", "e3", "e4", "e5", "e6", "e7"].map((typeKey) => (
                    <Grid item xs={3} sm={3} md={1.5} key={typeKey}>
                        {props.darkMode === false && (
                            <Button
                                variant={type === typeKey ? "contained" : "outlined"}
                                size="large"
                                onClick={() => handleTypeChange(typeKey)}
                                sx={{
                                    borderColor: "#000000",
                                    borderRadius: 1,
                                    borderWidth: 1,
                                    backgroundColor: type === typeKey ? dispColor(typeKey, 2) : "#FFFFFF",
                                    "&:hover": {
                                        backgroundColor: type === typeKey ? dispColor(typeKey, 3) : dispColor(typeKey, 1),
                                        borderColor: dispColor(typeKey, 2),
                                        borderWidth: 1,
                                    },
                                    color: type === typeKey ? "#FFFFFF" : "#000000",
                                    fontWeight: "bold",
                                    width: "100%",
                                }}
                            >
                                {typeKey === "e0" && "全集數"}
                                {typeKey !== "e0" && episodesType[typeKey]}
                            </Button>
                        )}
                        {props.darkMode === true && (
                            <Button
                                variant={"contained"}
                                size="large"
                                onClick={() => handleTypeChange(typeKey)}
                                sx={{
                                    backgroundColor: type === typeKey ? "#DDDDDD" : "#666666",
                                    "&:hover": {
                                        backgroundColor: type === typeKey ? "#FFFFFF" : "#888888",
                                        borderColor: dispColor(typeKey, 2),
                                        borderWidth: 1,
                                    },
                                    color: type === typeKey ? "black" : "white",
                                    fontWeight: "bold",
                                    width: "100%",
                                }}
                            >
                                {typeKey === "e0" && "全集數"}
                                {typeKey !== "e0" && episodesType[typeKey]}
                            </Button>
                        )}
                    </Grid>
                ))}
            </Grid>

            <Box container display={"flex"} spacing={2} justifyContent={"center"}>
                <Box mr={1}>
                    {props.darkMode === false && (
                        <Button
                            variant={"contained"}
                            sx={{
                                backgroundColor: listStyle === 0 ? grey[700] : grey[300],
                                "&:hover": {
                                    backgroundColor: listStyle === 0 ? grey[900] : grey[900],
                                    borderColor: "black",
                                    borderRadius: 1,
                                    borderWidth: 1,
                                    color: "white",
                                },
                                color: "white",
                            }}
                            onClick={() => onSetListStyle(0)}
                        >
                            <ViewModuleIcon />
                        </Button>
                    )}
                    {props.darkMode === true && (
                        <Button
                            variant={"contained"}
                            sx={{
                                backgroundColor: listStyle === 0 ? "#DDDDDD" : "#666666",
                                "&:hover": {
                                    backgroundColor: listStyle === 0 ? "#FFFFFF" : "#888888",
                                    borderColor: "black",
                                    borderRadius: 1,
                                    borderWidth: 1,
                                    color: listStyle === 0 ? "black" : "white",
                                },
                                color: listStyle === 0 ? "black" : "white",
                            }}
                            onClick={() => onSetListStyle(0)}
                        >
                            <ViewModuleIcon />
                        </Button>
                    )}
                </Box>
                <Box ml={1}>
                    {props.darkMode === false && (
                        <Button
                            variant={"contained"}
                            sx={{
                                backgroundColor: listStyle === 1 ? grey[700] : grey[300],
                                "&:hover": {
                                    backgroundColor: listStyle === 1 ? grey[900] : grey[900],
                                    borderColor: "black",
                                    borderRadius: 1,
                                    borderWidth: 1,
                                    color: "white",
                                },
                                color: "white",
                            }}
                            onClick={() => onSetListStyle(1)}
                        >
                            <ViewStreamIcon />
                        </Button>
                    )}
                    {props.darkMode === true && (
                        <Button
                            variant={"contained"}
                            sx={{
                                backgroundColor: listStyle === 1 ? "#DDDDDD" : "#666666",
                                "&:hover": {
                                    backgroundColor: listStyle === 1 ? "#FFFFFF" : "#888888",
                                    borderColor: "black",
                                    borderRadius: 1,
                                    borderWidth: 1,
                                    color: listStyle === 1 ? "black" : "white",
                                },
                                color: listStyle === 1 ? "black" : "white",
                            }}
                            onClick={() => onSetListStyle(1)}
                        >
                            <ViewStreamIcon />
                        </Button>
                    )}
                </Box>
            </Box>

            <Box mt={2}>
                {listStyle === 0 && (
                    <Grid container spacing={2} alignItems="stretch">
                        {currentItems.map((x, i) => {
                            return (
                                <Grid item xs={3} sm={2} md={1} key={i}>
                                    {props.darkMode === false && (
                                        <LightTooltip title={episodesData.episodesNameTW[Number(x) - 1]} arrow>
                                            <Button
                                                sx={{
                                                    backgroundColor: dispColor("e0", 2),
                                                    "&:hover": {
                                                        backgroundColor: dispColor("e0", 3),
                                                    },
                                                    color: "white",
                                                }}
                                                variant="contained"
                                                fullWidth
                                                onClick={() => {
                                                    setOpenDetail(true);
                                                    onShowEpisodeDetails(Number(x) - 1);
                                                }}
                                                style={{ height: "100%" }}
                                            >
                                                <Box fontSize={18} margin={"none"}>
                                                    {x}
                                                </Box>
                                            </Button>
                                        </LightTooltip>
                                    )}
                                    {props.darkMode === true && (
                                        <LightTooltip title={episodesData.episodesNameTW[Number(x) - 1]} arrow>
                                            <Button
                                                sx={{
                                                    backgroundColor: "#666666",
                                                    "&:hover": {
                                                        backgroundColor: "#888888",
                                                    },
                                                    color: "white",
                                                }}
                                                variant="contained"
                                                fullWidth
                                                onClick={() => {
                                                    setOpenDetail(true);
                                                    onShowEpisodeDetails(Number(x) - 1);
                                                }}
                                                style={{ height: "100%" }}
                                            >
                                                <Box fontSize={18} margin={"none"}>
                                                    {x}
                                                </Box>
                                            </Button>
                                        </LightTooltip>
                                    )}
                                </Grid>
                            );
                        })}
                    </Grid>
                )}

                {listStyle === 1 && (
                    <Grid container spacing={2} alignItems="stretch">
                        {currentItems.map((x, i) => {
                            return (
                                <Grid item xs={12} sm={12} md={6} key={i}>
                                    {props.darkMode === false && (
                                        <Button
                                            sx={{
                                                position: "relative",
                                                backgroundColor: dispColor("e0", 2),
                                                "&:hover": {
                                                    backgroundColor: dispColor("e0", 3),
                                                },
                                                color: "white",
                                            }}
                                            variant="contained"
                                            fullWidth
                                            onClick={() => {
                                                setOpenDetail(true);
                                                onShowEpisodeDetails(Number(x) - 1);
                                            }}
                                            style={{ height: "100%" }}
                                        >
                                            <Box
                                                fontSize={18}
                                                margin={"none"}
                                                sx={{
                                                    textTransform: "none",
                                                }}
                                            >
                                                {"Ep." + x + " " + episodesData.episodesNameTW[Number(x) - 1]}
                                            </Box>
                                        </Button>
                                    )}
                                    {props.darkMode === true && (
                                        <Button
                                            sx={{
                                                position: "relative",
                                                backgroundColor: "#666666",
                                                "&:hover": {
                                                    backgroundColor: "#888888",
                                                },
                                                color: "white",
                                            }}
                                            variant="contained"
                                            fullWidth
                                            onClick={() => {
                                                setOpenDetail(true);
                                                onShowEpisodeDetails(Number(x) - 1);
                                            }}
                                            style={{ height: "100%" }}
                                        >
                                            <Box
                                                fontSize={18}
                                                margin={"none"}
                                                sx={{
                                                    textTransform: "none",
                                                }}
                                            >
                                                {"Ep." + x + " " + episodesData.episodesNameTW[Number(x) - 1]}
                                            </Box>
                                        </Button>
                                    )}
                                </Grid>
                            );
                        })}
                    </Grid>
                )}

                <Box mt={5} display="flex" justifyContent="center" alignItems="center">
                    {props.darkMode === false && (
                        <>
                            <Button
                                variant="contained"
                                onClick={handlePreviousPage}
                                disabled={currentPage === 1}
                                sx={{
                                    marginRight: 2,
                                }}
                            >
                                上一頁
                            </Button>
                            <Select value={currentPage} onChange={handlePageSelect} displayEmpty inputProps={{ "aria-label": "Select page" }} sx={{ marginRight: 2, width: "110px" }} size="small">
                                {Array.from({ length: totalPages }, (_, i) => (
                                    <MenuItem key={i + 1} value={i + 1}>
                                        第 {i + 1} 頁
                                    </MenuItem>
                                ))}
                            </Select>
                            <Button variant="contained" onClick={handleNextPage} disabled={currentPage === totalPages}>
                                下一頁
                            </Button>
                        </>
                    )}
                    {props.darkMode === true && (
                        <>
                            <Button
                                variant="contained"
                                onClick={handlePreviousPage}
                                disabled={currentPage === 1}
                                sx={{
                                    marginRight: 2,
                                    backgroundColor: currentPage === 1 ? "#222222" : "#666666",
                                    color: "#FFFFFF",
                                    "&:hover": {
                                        backgroundColor: currentPage === 1 ? "#222222" : "#888888",
                                    },
                                    "&:disabled": {
                                        backgroundColor: "#222222",
                                        color: "#888888",
                                    },
                                }}
                            >
                                上一頁
                            </Button>
                            <Select
                                value={currentPage}
                                onChange={handlePageSelect}
                                displayEmpty
                                inputProps={{ "aria-label": "Select page" }}
                                sx={{
                                    marginRight: 2,
                                    width: "110px",
                                    backgroundColor: "#333333",
                                    color: "#FFFFFF",
                                    "& .MuiOutlinedInput-notchedOutline": {
                                        borderColor: "#555555",
                                    },
                                    "&:hover .MuiOutlinedInput-notchedOutline": {
                                        borderColor: "#888888",
                                    },
                                    "& .MuiSvgIcon-root": {
                                        color: "#FFFFFF",
                                    },
                                    "& .MuiPaper-root": {
                                        backgroundColor: "#333333",
                                        color: "#FFFFFF",
                                    },
                                }}
                                size="small"
                                MenuProps={{
                                    PaperProps: {
                                        sx: {
                                            backgroundColor: "#333333",
                                            color: "#FFFFFF",
                                        },
                                    },
                                }}
                            >
                                {Array.from({ length: totalPages }, (_, i) => (
                                    <MenuItem key={i + 1} value={i + 1}>
                                        第 {i + 1} 頁
                                    </MenuItem>
                                ))}
                            </Select>
                            <Button
                                variant="contained"
                                onClick={handleNextPage}
                                disabled={currentPage === totalPages}
                                sx={{
                                    backgroundColor: currentPage === totalPages ? "#222222" : "#666666",
                                    color: "#FFFFFF",
                                    "&:hover": {
                                        backgroundColor: currentPage === totalPages ? "#222222" : "#888888",
                                    },
                                    "&:disabled": {
                                        backgroundColor: "#222222",
                                        color: "#888888",
                                    },
                                }}
                            >
                                下一頁
                            </Button>
                        </>
                    )}
                </Box>
            </Box>
        </>
    );
};

export default EpisodeList;
