import { Box, createTheme, Grid, Switch, ThemeProvider } from "@mui/material";
import React, { useState } from "react";

const darkTheme = createTheme({
    palette: {
        mode: "dark",
        background: {
            default: "#333333",
            paper: "#424242",
        },
        text: {
            primary: "#ffffff",
            secondary: "#bbbbbb",
        },
    },
});

const lightTheme = createTheme({
    palette: {
        mode: "light",
        background: {
            default: "#ffffff",
            paper: "#f5f5f5",
        },
        text: {
            primary: "#000000",
            secondary: "#555555",
        },
    },
});

const EpisodeDetail = (props) => {
    const [showCharacterImg, setShowCharacterImg] = useState(true);

    const dispEpisodesTypeTextColor = (index) => {
        if (index === 0) {
            return "#FF0000";
        }
        if (index === 1) {
            return "#FF00AA";
        }
        if (index === 2) {
            return "#00BBFF";
        }
        if (index === 3) {
            return "#F4CB00";
        }
        if (index === 4) {
            return "#00AA00";
        }
        if (index === 5) {
            return "#777777";
        }
        if (index === 6) {
            return "#7A0099";
        }
    };

    const onCharacterImgShow = (e) => {
        setShowCharacterImg(e.target.checked);
    };

    return (
        <ThemeProvider theme={props.darkMode === false ? lightTheme : darkTheme}>
            <>
                {props.darkMode === false && (
                    <Box mt={3} boxShadow={1} sx={{ p: 1, border: "1px solid #ddd", borderRadius: 1 }}>
                        <Box>
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={4}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="black" fontWeight={"bold"} fontSize={18} textAlign="center">
                                        海外版：Ep.{props.detailData.episodeDetail.episodesIndexTW}
                                    </Box>
                                </Grid>
                                <Grid item xs={12} sm={4}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="black" fontWeight={"bold"} fontSize={18} textAlign="center">
                                        日版集數：Ep.{props.detailData.episodeDetail.episodesIndexJP}
                                    </Box>
                                </Grid>
                                <Grid item xs={12} sm={4}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="black" fontWeight={"bold"} fontSize={18} textAlign="center">
                                        播放時間：{props.detailData.episodeDetail.broadcastTime}
                                    </Box>
                                </Grid>
                            </Grid>
                        </Box>
                        <Box mt={2}>
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={6}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="black" fontWeight="bold" fontSize={18} textAlign="center">
                                        收視率：{props.detailData.episodeDetail.ratings}
                                    </Box>
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="black" fontWeight="bold" fontSize={18} textAlign="center">
                                        對應漫畫：{props.detailData.episodeDetail.mangaVolume === "-" ? "無" : props.detailData.episodeDetail.mangaVolume}
                                    </Box>
                                </Grid>
                            </Grid>
                        </Box>
                    </Box>
                )}
                {props.darkMode === true && (
                    <Box mt={3} boxShadow={1} sx={{ p: 1, border: "1px solid #ddd", borderRadius: 1, backgroundColor: "#222222" }}>
                        <Box>
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={4}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="#EEEEEE" fontWeight={"bold"} fontSize={18} textAlign="center">
                                        海外版：Ep.{props.detailData.episodeDetail.episodesIndexTW}
                                    </Box>
                                </Grid>
                                <Grid item xs={12} sm={4}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="#EEEEEE" fontWeight={"bold"} fontSize={18} textAlign="center">
                                        日版集數：Ep.{props.detailData.episodeDetail.episodesIndexJP}
                                    </Box>
                                </Grid>
                                <Grid item xs={12} sm={4}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="#EEEEEE" fontWeight={"bold"} fontSize={18} textAlign="center">
                                        播放時間：{props.detailData.episodeDetail.broadcastTime}
                                    </Box>
                                </Grid>
                            </Grid>
                        </Box>
                        <Box mt={2}>
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={6}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="#EEEEEE" fontWeight="bold" fontSize={18} textAlign="center">
                                        收視率：{props.detailData.episodeDetail.ratings}
                                    </Box>
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" color="#EEEEEE" fontWeight="bold" fontSize={18} textAlign="center">
                                        對應漫畫：{props.detailData.episodeDetail.mangaVolume === "-" ? "無" : props.detailData.episodeDetail.mangaVolume}
                                    </Box>
                                </Grid>
                            </Grid>
                        </Box>
                    </Box>
                )}

                <Box mt={3} boxShadow={1} sx={{ p: 1, border: "1px solid #ddd", borderRadius: 1, backgroundColor: props.darkMode === false ? "white" : "#222222" }}>
                    <Grid container spacing={2}>
                        <Grid container item xs={12} spacing={2}>
                            {Object.keys(props.detailData.episodesType).map((key, i) => {
                                const episodeType = props.detailData.episodesType[key];
                                const episodeTypeStatus = props.detailData.characterDetail[key];

                                return (
                                    <>
                                        {props.darkMode === false && (
                                            <Grid item xs={12 / Object.keys(props.detailData.episodesType).length} key={key}>
                                                <Box
                                                    display="flex"
                                                    justifyContent="center"
                                                    alignItems="center"
                                                    textAlign="center"
                                                    color={episodeTypeStatus === "T" ? dispEpisodesTypeTextColor(i) : "black"}
                                                    fontWeight="bold"
                                                    fontSize={16}
                                                    sx={{ width: "100%" }}
                                                >
                                                    {episodeType}
                                                </Box>
                                            </Grid>
                                        )}
                                        {props.darkMode === true && (
                                            <Grid item xs={12 / Object.keys(props.detailData.episodesType).length} key={key}>
                                                <Box
                                                    display="flex"
                                                    justifyContent="center"
                                                    alignItems="center"
                                                    textAlign="center"
                                                    color={episodeTypeStatus === "T" ? "#EEEEEE" : "#666666"}
                                                    fontWeight="bold"
                                                    fontSize={16}
                                                    sx={{ width: "100%" }}
                                                >
                                                    {episodeType}
                                                </Box>
                                            </Grid>
                                        )}
                                    </>
                                );
                            })}
                        </Grid>

                        <Grid container item xs={12} spacing={2}>
                            {Object.keys(props.detailData.episodesType).map((key, i) => {
                                const episodeTypeStatus = props.detailData.characterDetail[key];

                                return (
                                    <>
                                        {props.darkMode === false && (
                                            <Grid item xs={12 / Object.keys(props.detailData.episodesType).length} key={key}>
                                                <Box
                                                    display="flex"
                                                    justifyContent="center"
                                                    alignItems="center"
                                                    textAlign="center"
                                                    color={episodeTypeStatus === "T" ? dispEpisodesTypeTextColor(i) : "black"}
                                                    fontSize={30}
                                                    sx={{ width: "100%" }}
                                                >
                                                    {episodeTypeStatus === "T" ? "★" : "\u00A0"}
                                                </Box>
                                            </Grid>
                                        )}
                                        {props.darkMode === true && (
                                            <Grid item xs={12 / Object.keys(props.detailData.episodesType).length} key={key}>
                                                <Box
                                                    display="flex"
                                                    justifyContent="center"
                                                    alignItems="center"
                                                    textAlign="center"
                                                    color={episodeTypeStatus === "T" ? "#EEEEEE" : "#666666"}
                                                    fontSize={30}
                                                    sx={{ width: "100%" }}
                                                >
                                                    {episodeTypeStatus === "T" ? "★" : "\u00A0"}
                                                </Box>
                                            </Grid>
                                        )}
                                    </>
                                );
                            })}
                        </Grid>
                    </Grid>
                </Box>

                <Grid container alignItems="center" justifyContent="space-between" mt={6}>
                    <Grid item xs={4} />
                    <Grid item xs={4}>
                        <Box textAlign="center" fontSize={24} fontWeight="bold">
                            出場角色
                        </Box>
                    </Grid>
                    <Grid item xs={4}>
                        <Box display="flex" justifyContent="flex-end" alignItems="center">
                            <Box>
                                顯示
                                <br />
                                圖片
                            </Box>
                            <Switch onChange={onCharacterImgShow} checked={showCharacterImg} color="primary" />
                        </Box>
                    </Grid>
                </Grid>

                <Box mt={2}>
                    <Grid container spacing={1}>
                        {Object.keys(props.detailData.charatersList).map((key) => {
                            const character = props.detailData.charatersList[key];
                            const characterStatus = props.detailData.characterDetail[key];

                            return (
                                <>
                                    {props.darkMode === false && (
                                        <Grid item xs={4} sm={2} md={1} key={key}>
                                            <Box
                                                display="flex"
                                                flexDirection="column"
                                                alignItems="center"
                                                textAlign="center"
                                                sx={{
                                                    border: "1px solid #ddd",
                                                    borderRadius: 1,
                                                    p: 1,
                                                    bgcolor: characterStatus === "T" ? "#FFF6A3" : "#FFFFFF",
                                                    boxShadow: 1,
                                                }}
                                            >
                                                {showCharacterImg && (
                                                    <Box
                                                        component="img"
                                                        src={`img/${key}.png`}
                                                        sx={{
                                                            width: 70,
                                                            height: 70,
                                                            objectFit: "cover",
                                                            borderRadius: 1,
                                                            filter: characterStatus === "T" ? "none" : "grayscale(100%)",
                                                            opacity: characterStatus === "T" ? 1 : 0.5,
                                                        }}
                                                        mb={1}
                                                    />
                                                )}
                                                <Box color={characterStatus === "T" ? "black" : "#BBBBBB"} fontWeight="bold" fontSize={14}>
                                                    {character}
                                                </Box>
                                            </Box>
                                        </Grid>
                                    )}
                                    {props.darkMode === true && (
                                        <Grid item xs={4} sm={2} md={1} key={key}>
                                            <Box
                                                display="flex"
                                                flexDirection="column"
                                                alignItems="center"
                                                textAlign="center"
                                                sx={{
                                                    border: characterStatus === "T" ? "#EEEEEE" : "#333333",
                                                    borderRadius: 1,
                                                    p: 1,
                                                    bgcolor: characterStatus === "T" ? "#EEEEEE" : "#333333",
                                                    boxShadow: 1,
                                                }}
                                            >
                                                {showCharacterImg && (
                                                    <Box
                                                        component="img"
                                                        src={`img/${key}.png`}
                                                        sx={{
                                                            width: 70,
                                                            height: 70,
                                                            objectFit: "cover",
                                                            borderRadius: 1,
                                                            filter: characterStatus === "T" ? "none" : "grayscale(100%)",
                                                            opacity: characterStatus === "T" ? 1 : 0.5,
                                                        }}
                                                        mb={1}
                                                    />
                                                )}
                                                <Box color={characterStatus === "T" ? "black" : "#666666"} fontWeight="bold" fontSize={14}>
                                                    {character}
                                                </Box>
                                            </Box>
                                        </Grid>
                                    )}
                                </>
                            );
                        })}
                    </Grid>
                </Box>
            </>
        </ThemeProvider>
    );
};

export default EpisodeDetail;
